.post-screen {
    background-color: rgba(0, 0, 0, 0.242);
    z-index: 5;
    outline: 5px solid rgb(0, 0, 0);
    outline-offset: -5px;
    position: fixed;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
    display: flex;
}



.dl {
    /* background-color: #ffffff; */
    display: flex;
  }
  
  .video-container {
    flex: 1;
    position: relative;
    /* padding-bottom: 56.4999%; */
    height: 0;
  }
  
  .video-container iframe {
    border-radius: 5px;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: block;
  }


.post-container {
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    background-color: white;
}

.post-user-info {
 
    padding-top: 15px;
    padding-bottom: 15px;
    display: flex;
    align-items: center;
    gap: 5px;
}

.post-user-profile {
    width: 30px;
    height: 30px;
    border-radius: 15px;
    margin-left: 15px;
}

.post-user-profile-image {
    width: 30px;
    object-fit: cover;
    border-radius: 15px;
}

/* IMAGE POST */
.post-image-content {
    border-radius: 5px;
    border: 14px solid white;
    /* background-color: rgb(255, 255, 255); */
}

.img {
    border-radius: 5px;
}


/* TEXT POST*/

.post-text-content {
    padding-left: 25px;
    padding-right: 25px;

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    padding-top: 10px;
    padding-bottom: 10px;

}

.post-text-title {
    font-size: 23px;
    font-weight: bold;
    margin-bottom: 10px;
}

.post-text-text {

    font-size: 18px;
}

/* CITA POST*/

.post-cita-content {
    /* outline: 1px solid red;
    outline-offset: -1px; */
    /* background-color: white; */

    display: flex;
    justify-content: center;

    padding-left: 50px;
    padding-right: 50px;

    padding-top: 30px;
    padding-bottom: 10px;
}



.post-text-comilla-left {
    width: 25px;
    height: auto;
    filter: contrast(20%);
}

.post-cita-text-container {
    display: flex;
    flex-direction: column;
    /* outline: 1px solid red; */
}

.post-cita-text {

    font-size: 23px;
    padding-top: 20px;
    padding-bottom: 20px;
    font-style: italic;
}

.post-text-right {

    display: flex;
}

.post-text-comilla-right {
    width: 25px;
    height: auto;
    align-self: flex-end;
    transform: rotate(180deg);
    filter: contrast(20%);
}

.post-cita-detail {
    /* background-color: white; */
    padding-left: 70px;
}

/* VIDEO POST*/


/* ALL TYPES OF POSTS*/

.post-sub {
    font-family: Circular-light;
    /* background-color: rgb(255, 255, 255); */
    padding: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    border-top: 1px solid rgb(245, 245, 245);

}

.post-sub-header {
    display: inline;
    margin-left: 5px;
}

.post-sub-text {
    min-height: 30px;
}

.social-icons {

    align-self: flex-end;
    display: flex;
    gap: 20px;
    margin-right: 12px;
    margin-bottom: 5px;
}

.social-icon {
    background-size: cover;
    height: 20px;
    width: 19px;
    
}

.social-like {
    background-image: url('../icons/corazon.jpg');
}


.social-comment {
    background-image: url('../icons/comentario.jpg');
}

.social-share {
    background-image: url('../icons/compartir.jpg');
    width: 21px;
}

.social-delete {
    background-image: url('../icons//delete-icon.png');
}